
import {defineComponent, onUpdated, reactive, ref} from 'vue'
import {useStore} from "vuex";
import {ActionType} from "@/store/actions";

export default defineComponent({
  name: 'CameraDialog',

  props: {
    cameraVisible: Boolean,
    basketId: Number,
  },

  setup(props, {emit}) {
    const store = useStore();

    const videoRef = ref<HTMLVideoElement | null>(null);
    const canvasRef = ref<HTMLCanvasElement | null>(null);
    const width = 320;    // We will scale the photo width to this
    let height = 0;       // This will be computed based on the input stream
    let streaming = false;

    const state = reactive({
      cameraVisible: props.cameraVisible,
    });

    onUpdated(() => {
      state.cameraVisible = props.cameraVisible;
      if (state.cameraVisible) {
        startVideoCamera(videoRef.value, canvasRef.value);
      }
    });

    const takePicture = () => {
      if (canvasRef.value !== null && videoRef.value !== null) {
        console.log("CameraDialog.takePicture: take a picture of the currently displayed video stream");

        console.log("takePicture1 " + width + ", " + height);
        if (width && height) {
          console.log("takePicture2 " + width + ", " + height);

          canvasRef.value.width = width;
          canvasRef.value.height = height;

          const context = canvasRef.value.getContext('2d');
          if (context !== null) {
            context.drawImage(videoRef.value, 0, 0, width, height);
            if (props.basketId) {
              let data = canvasRef.value.toDataURL('image/png');
              store.dispatch(ActionType.StoreBasketImage, {basketId: props.basketId.valueOf(), imageDataUrl: data});
            }
          }
        }
        closeCamera();

      } else {
        console.log("CameraDialog.takePicture: canvas, video or photo is null");
      }
    }

    const startVideoCamera = (video: HTMLVideoElement | null, canvas: HTMLCanvasElement | null) => {
      if (video !== null && canvas !== null) {
        console.log("CameraDialog.startVideoCamera: Start video camera");

        navigator.mediaDevices.getUserMedia({video: true, audio: false})
            .then(function (videoStream: MediaStream) {
              if (video !== null) {
                video.srcObject = videoStream;
                video.play();
              }
            })
            .catch(function (err) {
              console.log("Could not access user camera: no compatible camera found or access denied. " + err);
            });


        video.addEventListener('canplay', function () {
          if (!streaming) {
            height = video.videoHeight / (video.videoWidth / width);

            video.setAttribute('width', String(width));
            video.setAttribute('height', String(height));
            canvas.setAttribute('width', String(width));
            canvas.setAttribute('height', String(height));
            streaming = true;
          }
        }, false);
      } else {
        console.log("CameraDialog.startVideoCamera: canvas or video is null");
      }
    }

    const stopVideoCamera = (video: HTMLVideoElement | null) => {
      console.log("CameraDialog.stopVideoCamera: Stop video camera");
      if (video !== null) {
        let stream: MediaStream = video.srcObject as MediaStream;
        stream.getTracks().forEach((track) => {
          track.stop()
        });
      }
    }

    const closeCamera = () => {
      console.log("CameraDialog.closeCamera");
      stopVideoCamera(videoRef.value);
      emit('close-camera');
    }

    return {
      state,
      canvasRef,
      videoRef,
      width,
      height,
      takePicture,
      startVideoCamera,
      stopVideoCamera,
      closeCamera
    }
  }

})
